<template>
    <div>
        <van-address-edit
        show-postal
        show-delete
        show-set-default
        show-search-result
        :show-area="false"
        :search-result="searchResult"
        @save="onSave"
        @delete="onDelete"
        @change-detail="onChangeDetail"
        />
    </div>
</template>

<script>
import { Toast } from 'vant';

export default {
        data() {
        return {
        searchResult: [],
        };
    },
    methods: {
        onSave() {
            Toast('保存成功');
        },
        onDelete() {
            Toast('已删除');
            this.$router.push({
                name: "address",
            })
        },
        onChangeDetail(val) {
            if (val) {
                // this.searchResult = [
                // {
                //     name: '黄龙万科中心',
                //     address: '杭州市西湖区',
                // },
                // ];
            } else {
                this.searchResult = [];
            }
        },
    },
}
</script>

